import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getEspecialidad } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'

const Especialidad = ({especialidad}) => {

  useEffect(function() {
    store.dispatch(getEspecialidad(1))
  }, [])


  return (

    <>
      {
        especialidad &&
        (
          <>
            <Banner
              color="dark-color"
              title={especialidad.data.name}
              subtitle={especialidad.data.subtitle}
              imagen={{
                src: "https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg",
                alt: especialidad.data.name
              }}
              cursoBanner
              poster={especialidad.data.picture}
              info={especialidad.data.information}
            />
            <main className="ed-grid lg-grid-10">
              <div className="lg-cols-7">
                <div className="course-feature">

                </div>
                <h2>Temaria de la especialidad</h2>
                <div className="s-border s-pxy-2 lg-pxy-4 s-radius s-bg-white l-block l-section s-shadow-bottom">
                  {
                    especialidad.data.courses.map(function(objCursos) {
                      return (
                        <div className="course-class l-section" key={objCursos.id}>
                          <div className="ed-grid m-grid-3">
                            <img src={objCursos.picture} alt={objCursos.name} />
                            <div className="m-cols-2">
                              <h3>{objCursos.name}</h3>
                              <p>{objCursos.information}</p>
                            </div>
                          </div>
                          
                        </div>
                      )
                    })
                  }

                </div>
              </div>
              
            </main>
          </>
        )
      }
    </>


    
  )
}

const mapStateToProps = state => ({
  especialidad: state.especialidadReducer.especialidad
})

export default connect(mapStateToProps, {})(Especialidad)
