import React from 'react'
import axios from "axios"
import {Link, useHistory} from "react-router-dom"

const Login = () => {
  const history = useHistory()
  const autentificacion = function(e) {
    e.preventDefault()
    const form = e.target
    const data = {
      correo: form.correo.value,
      password: form.contrase√±a.value
    }

    axios.post("https://punto-de-venta-codigo-backend.herokuapp.com/login", 
      JSON.stringify(data),
      {
        headers: {
          "Content-type": "application/json"
        }
      }
    ).then(function(respuesta) {
      console.log(respuesta)
      localStorage.setItem("token", respuesta.data.token)
      history.push("/")

    }).catch(function(error) {
      console.log(error)
      alert("Error al iniciar sesion")
    })
  }

  return (
    <div className="ed-grid">
      <div className="l-block"></div>
      <div className="m-to-center m-60 lg-30">
        <h1 className="center">Iniciar Sesion</h1>
        <form onSubmit={autentificacion.bind()}>
          <div className="form__item">
            <label htmlFor="email">
              Correo electronico
              <input type="email" name="correo" id="email" placeholder="Ingrese su email" required/>
            </label>
          </div>
          <div className="form__item">
            <label htmlFor="password">
              Contrase√±a
              <input type="password" name="contrase√±a" id="password" placeholder="Ingrese su contrase√±a" required/>
            </label>
          </div>
          <div className="form__item">
            <input type="submit" className="button full" value="Iniciar Sesion" />
          </div>
        </form>
        <div className="center">
          <p>¬øNo tienes cuenta de usuario? <Link to="/registro">Crear cuenta</Link></p>
        </div>
      </div>
    </div>
  )
}

export default Login
