import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getCurso } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'

const Curso = ({curso}) => {

  useEffect(function() {
    store.dispatch(getCurso(1))
  })

  return (
    <>
      {
        curso &&
        (
          <>
            <Banner
              color="dark-color"
              title={curso.name}
              subtitle={curso.subtitle}
              imagen={{
                src: "https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg",
                alt: curso.name
              }}
              cursoBanner
              poster={curso.picture}
              especialidad={curso.data.specialities[0].name}
              info={curso.information}
            />
            <main className="ed-grid lg-grid-10">
              <div className="lg-cols-7">
                <div className="course-feature">

                </div>
                <h2>Temaria del curso</h2>
                <div className="s-border s-pxy-2 lg-pxy-4 s-radius s-bg-white l-block l-section s-shadow-bottom">
                  {
                    curso.data.classes.map(function(objClase) {
                      return (
                        <div className="course-class l-section" key={objClase.class.id}>
                          <h3>{objClase.class.title}</h3>
                          <p>{objClase.class.description}</p>
                          <ul>
                            {
                              objClase.subjects.map(function(objSub) {
                                return (
                                  <li>{objSub.subject.title}</li>
                                )
                              })
                            }
                          </ul>
                        </div>
                      )
                    })
                  }

                </div>
              </div>
              <div className="lg-cols-3">
                <h2 className="t3">Profesor</h2>
                <p>Luis Ynfante</p>
              </div>
            </main>
          </>
        )
      }
      
    </>
  )
}


const mapStateToProps = state => ({
  curso: state.cursoReducer.curso
})

export default connect(mapStateToProps, {})(Curso)
