import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getAllCursos } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'
import Card from '../Organisms/Card'

const Cursos = ({cursos}) => {

  useEffect(function() {
    store.dispatch(getAllCursos())
  }, [])

  return (
    <>
      <Banner
        color="dark-color"
        imagen={{
          src: "https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg",
          alt: ""
        }}
        title="Nuestros cursos"
        subtitle="Comienza desde cero para dominar las area de la ingenieria civil"
      />
      {
        cursos && (
          <main className="ed-grid m-grid-5">
            {
              cursos.map(function(objCurso) {
                return (
                  <Card picture={objCurso.picture} name={objCurso.name} key={objCurso.id} cardId={objCurso.id} path={"cursos"}/>
                )
              })
            }
          </main>
        )
      }
    </>
  )
}

const mapStateToProps = state => ({
  cursos: state.cursoReducer.cursos
})


export default connect(mapStateToProps, {})(Cursos)
