import React from 'react'
import {HashRouter, Route, Switch} from "react-router-dom"
import PrivateRoute from './routes/PrivateRoute'
import PublicRoute from './routes/PublicRoute'
import Home from './pages/Home'
import Especialidades from "./pages/Especialidades"
import Especialidad from "./pages/Especialidad"
import Cursos from "./pages/Cursos"
import Curso from "./pages/Curso"
import Profesores from "./pages/Profesores"
import Clase from "./pages/Clase"
import Login from "./pages/Login"
import Registro from "./pages/Registro"
import Page404 from "./pages/Page404"
import Header from './Organisms/Header'

const App = () => {
  return (
    <HashRouter>
      <Header/>
      <Switch>
        <PrivateRoute path="/" component={Home} exact/>
        <PrivateRoute path="/especialidades" component={Especialidades} exact/>
        <PrivateRoute path="/especialidades/:id" component={Especialidad} exact/>
        <PrivateRoute path="/cursos" component={Cursos} exact/>
        <PrivateRoute path="/cursos/:id" component={Curso} exact/>
        <PrivateRoute path="/profesores" component={Profesores} exact/>
        <PrivateRoute path="/clases/:id" component={Clase} exact/>

        <PublicRoute path="/login" component={Login} exact/>
        <PublicRoute path="/registro" component={Registro} exact/>

        <Route component={Page404}/>
      </Switch>
    </HashRouter>
  )
}

export default App
