import {
  GET_ALL_ARTICULOS,
  GET_ARTICULO,
  GET_ALL_ESPECIALIDADES,
  GET_ESPECIALIDAD,
  GET_ALL_CURSOS,
  GET_CURSO,
  GET_ALL_PROFESORES,
  GET_CLASE
} from "./action"

export const articuloReducer = function(state = {}, action) {
  if(action.type === GET_ALL_ARTICULOS) {
    return {
      ...state,
      articulos: action.articulos
    }
  }

  if(action.type === GET_ARTICULO) {
    return {
      ...state,
      articulo: action.articulo
    }
  }
  return state
}

