import React, { useEffect } from 'react'
import { getAllProfesores } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'
import {connect} from "react-redux"
import Profesor from '../Organisms/Profesor'

const Profesores = ({profesores}) => {

  useEffect(function() {
    store.dispatch(getAllProfesores())
  }, [])

  return (
    <>
      <Banner 
        color="second-color" 
        imagen={{
          src: "https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg",
          alt: "Banner profesores"
        }}
        title="Nuestros profesores"
        subtitle="Este plantel docente esta calificado para guiarte"/>
        {
          profesores && (
            <main className="ed-grid m-grid-3 lg-grid-4 row-gap">
              {
                profesores.map(function(objProfesor) {
                  return (
                    <Profesor
                      key={objProfesor.id}
                      picture={objProfesor.picture}
                      name={objProfesor.name}
                      country={objProfesor.country}
                    />
                  )
                })
              }
            </main>
          )
        }
    </>
  )
}

const mapStateToProps = state => ({
  profesores: state.profesorReducer.profesores
})

export default connect(mapStateToProps, {})(Profesores)


