import React from 'react'
import axios from "axios"
import {useHistory, Link} from "react-router-dom"

const Registro = () => {
  const history = useHistory()
  const crearCuenta = function(e) {
    e.preventDefault()
    const form = e.target
    const data = {
      usu_email: form.correo.value,
      use_nom: form.nombre.value,
      use_ape: "ninguno",
      usu_tipo: "admin",
      password: form.contrase√±a.value
    }
    
    axios.post("https://punto-de-venta-codigo-backend.herokuapp.com/registro",
      JSON.stringify(data),
      {
        headers: {
          "Content-type": "application/json"
        }
      }
    ).then(function() {
      alert("Cuenta creada")
      history.push("/login")
    }).catch(function() {
      alert("Error al crear cuenta")
    })
  }

  return (
    <div className="ed-grid">
      <div className="l-block"></div>
      <div className="m-to-center m-60 lg-30">
        <h1 className="center">Crear Cuenta</h1>
        <form onSubmit={crearCuenta.bind()}>
          <div className="form__item">
            <label htmlFor="fullname">
              Nombre Completo
              <input type="text" name="nombre" id="fullname" placeholder="Ingrese su nombre y apellido" required/>
            </label>
          </div>
          <div className="form__item">
            <label htmlFor="email">
              Correo electronico
              <input type="email" name="correo" id="email" placeholder="Ingrese su email" required/>
            </label>
          </div>
          <div className="form__item">
            <label htmlFor="password">
              Contrase√±a
              <input type="password" name="contrase√±a" id="password" placeholder="Ingrese su contrase√±a" required/>
            </label>
          </div>
          <div className="form__item">
            <input type="submit" className="button full" value="Crear Cuenta"/>
          </div>
        </form>
        <div className="center">
          <p>¬øYa tienes cuenta de usuario? <Link to="/login">Iniciar Sesion</Link></p>
        </div>
      </div>
    </div>
  )
}

export default Registro
