import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getAllEspecialidades } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'
import Card from '../Organisms/Card'

const Especialidades = ({especialidades}) => {

  useEffect(function() {
    store.dispatch(getAllEspecialidades())
  }, [])

  return (
    <>
      <Banner
        color="first-color"
        imagen={{
          src: "https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg",
          alt: "Banner Especialidades"
        }}
        title="Especialidades"
        subtitle="Domina una area de la ingenieria civil con los cursos que te ofrecemos"
      />
      {
        especialidades && (
          <main className="ed-grid m-grid-3">
            {
              especialidades.map(function(objEspecialidad) {
                return (
                  <Card picture={objEspecialidad.picture} name={objEspecialidad.name} key={objEspecialidad.id} cardId={objEspecialidad.id} path={"especialidades"}/>
                )
              })
            }
          </main>
        )
      }
    </>
    
  )
}

const mapStateToProps = state => ({
  especialidades: state.especialidadReducer.especialidades
})


export default connect(mapStateToProps, {})(Especialidades)
