import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getAllArticulos } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'
import Publicaciones from '../Organisms/Publicaciones'

const Home = ({articulos}) => {

  useEffect(function() {
    store.dispatch(getAllArticulos())
  })

  return (
    <>
      <Banner
        color="dark-color"
        imagen="https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg"
        title="Bienvenido a la experiencia mas increible de capacitacion online para ingenierieros civiles"
        subtitle="Nuestro equipo adesarrollado esta plataforma pensando en ti. Sabemos que estabas buscando contenido de calidad. Aqu√≠ lo encontraras"
        home
        poster="https://www.pensionescolombia.com/wp-content/uploads/2016/06/educacion-examen-universitariosshut.jpg"
      />
      <main className="ed-grid m-grid-3">
        <div className="l-section m-cols-2">
          <h2>Ultimas publicaciones</h2>
          {
            articulos ?
            (
              <div>
                {
                  articulos.map(function(objArticulo) {
                    return (
                      <Publicaciones title={objArticulo.title} author={objArticulo.author} fecha={objArticulo.fecha} content={objArticulo.content} key={objArticulo.id} />
                    )
                  })
                }
              </div>
            ) :
            <p>No existen publicaciones en la base de datos</p>
          }
        </div>
        <div>
          <h3>Lista de categorias</h3>
          <ul className="featured-list">
            <li><span>React js</span></li>
            <li><span>Angular</span></li>
            <li><span>Vue</span></li>
          </ul>
        </div>
      </main>
    </>
  )
}

const mapStateToProps = state => ({
  articulos: state.articuloReducer.articulos
})

export default connect(mapStateToProps, {})(Home)
